<template>
  <div class="r-course-list">
    <div v-if="course.length > 0">
      <div class="r-course-item" :key="i" v-for="(item, i) in course">
        <div class="tt-btn">
          <div class="tt">{{ item.CHName }}</div>
          <div class="btn">
            <button v-if="courseType === 'current'" @click="selectCourse(item)">
              点击加入
            </button>
            <button v-else-if="courseType !== 'current'" @click="selectCourse(item)">查看</button>
          </div>
        </div>
        <div class="course-name">
          【{{item.CourseName}}】
        </div>
        <div class="info-line">
          <div class="line-left">老师：{{ item.TeacherName }}</div>
          <div class="line-right">班级：{{ item.ClassName }}</div>
        </div>
        <div class="info-line">
          <div class="line-left">教室：{{ item.ClassroomName }}</div>
          <div class="line-right">
            时间：
            {{ item["CreateTime"] | formatDate(item["CreateTime"]) }}
          </div>
        </div>
        <div class="line"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {
    courseType: {
      type: String,
      default: "current"
    },
    course: {
      type: Array,
      default: () => {
        return [];
      }
    }
  },
  data() {
    return {
      sites: [1, 2, 3]
    };
  },
  created() {},
  methods: {
    selectCourse(item) {
      this.$store.commit("SET_COURSE", item);
      this.$store.commit("SET_COURSEID", item["CIID"]);

      this.$store
        .dispatch("GetCourseServiceByUser", item["ClassroomID"])
        .then(res => {
          if (res.Result) {
            if (this.courseType === "current") {
              this.$router.push("/course");
            } else {
              this.$router.push("/history");
            }
          } else if (res.Info) {
            this.$toast(res.Info);
          } else {
            this.$toast("获取教室服务失败");
          }
        })
        .catch(err => {
          this.$toast("获取教室服务出错");
        });
    }
  },
  computed: {},
  watch: {}
};
</script>


